@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.container {
  width: 100vw;
  height: 100vh;
  @include flexCenter();
  .wrapper {
    @include authWrapper(413px, 540px);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    .main {
      display: flex;
      flex-direction: column;
      .textAction {
        margin-top: 12px;
        margin-bottom: 70px;
        font-family: SF UI Display, sans-serif;
        font-size: 16px;
        line-height: 24px;
        color: #2d2e46;
        opacity: 0.5;
        text-align: left;
        letter-spacing: 0.2px;
      }
      .authBtn {
        @include authBtn();
      }
    }
    @include footer;
  }
}
