@import '../../styles/variables.scss';
@import '../../styles/mixins.scss';

.container {
  width: 100vw;
  height: 100%;
  @include flexCenter();
  .wrapper {
    @include authWrapper(413px, 500px);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    .header {
        @include flexCenter();
    }
    .table {
        flex-grow: 1;
        overflow: auto;
    }
    .footer {
        @include flexCenter();
    }
    
  }
}
